<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero(data-aos="fade-right")
        span 3
      h1 Emergencias y contingencias de operacin de transporte de animales en pie

    .row.justify-content-center.mb-4
      .col-lg-9.mb-4.mb-lg-0
        .bg-color-6.tarjeta.p-4
          .row.justify-content-center.align-items-center
            .col-lg-1.p-0.d-none.d-lg-block
              figure
                img.img-fluid(src="@/assets/curso/temas/tema3/img-01.svg" alt="Icono 1")
            .col-lg-11
              p Las #[b emergencias y contingencias] en el transporte de animales en pie son cualquier situacin crtica o inesperada que atenta contra el #[b bienestar, la salud o la vida] de la carga viva. Ante estos eventos, el personal operativo debe aplicar inmediatamente el #[b plan de Contingencia] con tcnicas de actuacin definidas para mitigar el sufrimiento, el dolor y la mortalidad.
              p.mb-0 Para aclarar un poco cada situacin y accin, la siguiente imagen detalla lo destacado en ellas: 
      .col-lg-3
        figure
          img.img-fluid(src="@/assets/curso/temas/tema3/img-02.png" alt="Icono 1")

    .row.justify-content-center.mb-4(data-aos="fade-left")
      .col-lg-10
        .titulo-sexto.color-acento-contenido.mb-3.mt-2
          h5 Figura 3.
          span <em>Emergencias, contingencias y protocolos de accin</em>    

        .row.bg-color-1.justify-content-center.align-items-center.p-4
          .col-lg-10
            figure.desktop
              img(src='@/assets/curso/temas/tema3/figura-1.svg', alt='Figura 3 que destaca por medio de una infografa las situaciones que se consideran como emergencias o contingencias y el protocolo de accin que se debe implementar para solventarlas.', style="width: 1043px").m-auto
            figure.movil
              img(src='@/assets/curso/temas/tema3/figura-2.svg', alt='Figura 3 que destaca por medio de una infografa las situaciones que se consideran como emergencias o contingencias y el protocolo de accin que se debe implementar para solventarlas.', style="width: 1043px").m-auto

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Situaciones que constituyen una emergencia (novedades)
    
    p.mb-4 Las siguientes condiciones, detectadas por el conductor o tripulante (generalmente durante la #[b inspeccin cada cuatro horas)], activan el protocolo de contingencia:

    .row.mb-4
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta.color-primarios2.p-4.h-100
          .row.mb-2
            .col-lg-4.col-4
              figure
                img(src='@/assets/curso/temas/tema3/img-26.svg', alt='Imagen decorativa')
          h5 Condicin sanitaria / lesin
          p.mb-0 Animales enfermos, lesionados (traumatismos, fracturas) que sufren dolor evidente.
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta.color-primarios2.p-4.h-100
          .row.mb-2
            .col-lg-4.col-4
              figure
                img(src='@/assets/curso/temas/tema3/img-27.svg', alt='Imagen decorativa')
          h5 Incapacidad de movilidad
          p.mb-0 Animales cados, atrapados (por la estructura o la carga) o que no pueden mantenerse en equilibrio esttico por s mismos.
      .col-lg-3.mb-4.mb-lg-0(data-aos="fade-right")
        .tarjeta.color-primarios2.p-4.h-100
          .row.mb-2
            .col-lg-4.col-4
              figure
                img(src='@/assets/curso/temas/tema3/img-28.svg', alt='Imagen decorativa')
          h5 Accidente vial
          p.mb-0 Cualquier accidente de trnsito o avera grave del vehculo que implique la afectacin directa del bienestar de los animales.
      .col-lg-3(data-aos="fade-right")
        .tarjeta.color-primarios2.p-4.h-100
          .row.mb-2
            .col-lg-4.col-4
              figure
                img(src='@/assets/curso/temas/tema3/img-29.svg', alt='Imagen decorativa')
          h5 Mortalidad
          p.mb-0 Presencia de animales muertos en la unidad de transporte.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img-03.svg' alt="Imagen decorativa")
          .tarjeta.color-primario2.w-100(style="padding-top: 5px !important")
            .text-white.p-4
              p.text-center.mb-0(style="margin-top: 42px;") #[b Soporte documental:] "Ante #[b animales enfermos, muertos o accidentes] que conlleven animales atrapados, cadas, traumatismos o fracturas de estos, durante el proceso de cargue, transporte y descargue de los animales...".

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Protocolo operacional y tcnicas de actuacin

    .fondo2-t2.px-md-5.px-4.py-4
      p.mb-4 El #[b propietario, poseedor, tenedor, conductor, transportador o tripulante] tiene la responsabilidad de ejecutar el plan de contingencia mediante las siguientes tres tcnicas:

      .titulo2.mb-4(data-aos="fade-left")
        h5.mb-0 A. Tcnica de accin inmediata (prioridad: bienestar)

      p.mb-4 El objetivo es mitigar el sufrimiento y el dao fsico de forma urgente, basado en lo siguiente:

      .row.justify-content-center.align-items-center.mb-5
        .col-lg-10.tarjeta.color-secundario.p-4(data-aos="fade-right")
          .row.justify-content-center.align-items-center
            .col-lg-6.order-lg-1.order-2
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Detencin segura]
                    br
                    | Detener el vehculo inmediatamente en un lugar seguro.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Ayuda o atencin inmediata]
                    br
                    | Prestar #[b ayuda o atencin inmediata al animal] afectado.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnica de liberacin]
                    br
                    | Liberar animales atrapados.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnica de acomodacin]
                    br
                    | Asistir y reacomodar a los animales cados o fatigados para asegurar que tengan el #[b espacio disponible] para levantarse.
            .col-lg-6.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
              figure
                img.img-fluid(src="@/assets/curso/temas/tema3/img-04.png" alt="Imagen decorativo")

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 B. Tcnica de reporte y notificacin
    
    p.mb-4 El protocolo de reporte es esencial para el manejo sanitario y legal de la novedad; adems, tiene en cuenta lo siguiente:

    .row.justify-content-center.mb-4
      .col-lg-5(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img-05.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Notificacin obligatoria
              p.text-center.mb-0 #[b Informar a la oficina del Instituto Colombiano Agropecuario (ICA) ms cercana] al lugar de la emergencia o accidente para recibir directrices de manejo.
      .col-lg-5(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img-06.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Coordinacin local
              p.text-center.mb-0 En casos de accidentes, coordinar con las #[b autoridades locales] (municipal o distrital) para la gestin de la emergencia.

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 C. Tcnica de disposicin final

    p.mb-4 Se aplica tras la notificacin, siguiendo las instrucciones del ICA y se debe basar en lo siguiente:

    .row.justify-content-center
      .col-12.movil.mb-4
        .tarjeta.tarjeta--BG04.p-4
          figure.mb-4
              img(src="@/assets/curso/temas/tema3/img-07.png", alt="alt")
          h4.mb-3 Sacrificio humanitario (eutanasia)
          p.mb-0 Si un animal presenta sufrimiento irreversible o lesiones incurables, el plan debe incluir el #[b sacrificio] realizado por personal #[b competente y capacitado], bajo el mtodo ms humanitario, para terminar el dolor.
      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0.desktop
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img-07.png", alt="alt")
          .crd_hover_txt--body
            .tit-tarj.p-3
              .ubicar
                .acordion__accion.acordion__accion__btn--b.h3.mb-0
                  i.fas.fa-angle-up
                h4.mb-3 Sacrificio humanitario (eutanasia)
            p.mt-3.px-4 Si un animal presenta sufrimiento irreversible o lesiones incurables, el plan debe incluir el #[b sacrificio] realizado por personal #[b competente y capacitado], bajo el mtodo ms humanitario, para terminar el dolor.
      .col-12.movil.mb-4
        .tarjeta.tarjeta--BG04.p-4
          figure.mb-4
              img(src="@/assets/curso/temas/tema3/img-08.png", alt="alt")
          h4.mb-3 Disposicin sanitaria
          p.mb-0 Implementar las #[b disposiciones de los animales muertos] (cadveres), de acuerdo con las directrices sanitarias para evitar la contaminacin ambiental y la dispersin de riesgos biolgicos.

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0.desktop
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img-08.png", alt="alt")
          .crd_hover_txt--body
            .tit-tarj.p-3
              .ubicar
                .acordion__accion.acordion__accion__btn--b.h3.mb-0
                  i.fas.fa-angle-up
                h4.mb-3 Disposicin sanitaria
            p.mt-3.px-4 Implementar las #[b disposiciones de los animales muertos] (cadveres), de acuerdo con las directrices sanitarias para evitar la contaminacin ambiental y la dispersin de riesgos biolgicos.

      .col-12.movil
        .tarjeta.tarjeta--BG04.p-4
          figure.mb-4
              img(src="@/assets/curso/temas/tema3/img-09.png", alt="alt")
          h4.mb-3 Resumen del plan de contingencia
          p.mb-0 Las medidas van desde la #[b ayuda o atencin inmediata al animal, informar a la oficina del Instituto Colombiano Agropecuario ICA ms cercana, sacrificio y disposiciones de los animales muertos.]

      .col-xl-4.col-lg-7.col-md-9.col-11.mb-4.mb-xl-0.desktop
        .crd_hover_txt(data-aos="flip-left")
          .crd_hover_txt--img
            figure
              img(src="@/assets/curso/temas/tema3/img-09.png", alt="alt")
          .crd_hover_txt--body
            .tit-tarj.p-3
              .ubicar
                .acordion__accion.acordion__accion__btn--b.h3.mb-0
                  i.fas.fa-angle-up
                h4.mb-3 Resumen del plan de contingencia
            p.mt-3.px-4 Las medidas van desde la #[b ayuda o atencin inmediata al animal, informar a la oficina del Instituto Colombiano Agropecuario ICA ms cercana, sacrificio y disposiciones de los animales muertos.]

    Separador

    #t_3_1.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 3.1 Protocolo de atencin de emergencias y planes de contingencia

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12.mb-4.mb-lg-0
        .bg-color-6.tarjeta.p-4
          .row.justify-content-center.align-items-center
            .col-lg-1.p-0.d-none.d-lg-block
              figure
                img.img-fluid(src="@/assets/curso/temas/tema3/img-10.svg" alt="Icono 1")
            .col-lg-11
              p Este protocolo es un procedimiento operativo de cumplimiento obligatorio, diseado para responder de forma inmediata a cualquier situacin crtica que comprometa el bienestar o la vida de los animales durante el cargue, el trayecto o el descargue.
              p.mb-0 El objetivo principal del protocolo es #[b mitigar el sufrimiento y el dolor] de los animales y garantizar el manejo sanitario de la novedad.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Responsabilidad y activacin del plan

    p.mb-4 El plan de contingencia debe ser conocido y ejecutado por todo el personal involucrado en la movilizacin: el #[b propietario, poseedor, tenedor, conductor, transportador o tripulante.]

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Situaciones que activan la contingencia

    p.mb-4 El protocolo se activa ante la deteccin de cualquier #[b novedad grave] durante la inspeccin rutinaria (cada 4 horas) o de forma inesperada y se presenta en lo siguiente:

    .row.mb-5
      .col-lg.col-md-6.tarjeta--tabla1.p-4(data-aos="fade-right")
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema3/img-11.svg' alt="Imagen decorativa")
        h4.text-center Animales con condicin sanitaria crtica
        p.text-center.mb-0 Animales #[b enfermos] o #[b lesionados] (con traumatismos o fracturas).
      .col-lg.col-md-6.tarjeta--tabla2.p-4(data-aos="flip-right")
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema3/img-12.svg' alt="Imagen decorativa")
        h4.text-center Problemas de movilidad y confinamiento
        p.text-center.mb-0 Animales #[b cados, atrapados] o que presenten imposibilidad de mantenerse en #[b equilibrio esttico] por s mismos.
      .col-lg.col-md-6.tarjeta--tabla1.p-4(data-aos="flip-left")
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema3/img-13.svg' alt="Imagen decorativa")
        h4.text-center Mortalidad
        p.text-center.mb-0 Deteccin de animales #[b muertos] dentro de la unidad de transporte.
      .col-lg.col-md-6.tarjeta--tabla2.p-4(data-aos="fade-left")
        .row.justify-content-center.mb-3
          .col-4
            img(src='@/assets/curso/temas/tema3/img-14.svg' alt="Imagen decorativa")
        h4.text-center Accidentes
        p.text-center.mb-0 Ocurrencia de #[b accidentes de trnsito] o averas graves que afecten directamente a los animales.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Fases de actuacin del protocolo

    p.mb-4 La atencin de la emergencia sigue una secuencia jerrquica de acciones: #[b mitigacin, reporte y disposicin sanitaria] y se da por medio de estas fases:

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 Fase A. Accin inmediata (mitigacin del sufrimiento)
    
    p.mb-4 Esta es la primera y ms urgente accin, enfocada en el bienestar animal y tiene presente:

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          h4 - Detencin segura
          .row.justify-content-center.align-items-center.mb-2
              .col-lg-12
                p El conductor debe detener el vehculo de forma segura, en un lugar que permita el acceso a la carga sin generar riesgos adicionales.
          figure.text-center
            img.img-fluid(src="@/assets/curso/temas/tema3/img-30.png" alt="Imagen decorativa")

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-primarios3.p-4(data-aos="fade-right")
          h4 - Ayuda o atencin inmediata al animal
          .row.justify-content-center.mb-2
            .col-lg-12
              p El personal debe proporcionar ayuda o atencin inmediata al animal afectado. Las tcnicas de actuacin incluyen:
            .col-lg-8.col-12.order-lg-2.order-2
              ul.lista-ul.mb-0
                li
                  i.fas.fa-horse-head
                  span #[b Liberacin:] liberar animales atrapados de la estructura o de otros animales.
                li
                  i.fas.fa-horse-head
                  span #[b Acomodacin:] asistir a los animales cados o fatigados, reubicndolos en una posicin ms cmoda y asegurando que tengan #[b espacio disponible].
                li
                  i.fas.fa-horse-head
                  span #[b Primeros auxilios:] aplicar las medidas necesarias para estabilizar lesiones menores o controlar el estrs trmico (ej. aspersin de agua).

            .col-lg-4.col-8.col-md-7.order-lg-1.order-1.mb-3
              figure
                img(src='@/assets/curso/temas/tema3/img-31.png', alt='Imagen decorativa')

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 Fase B. Reporte y notificacin obligatoria

    p.mb-4 Este paso es crtico para el cumplimiento legal y el manejo sanitario de la novedad y debe hacer por medio de:

    .row.justify-content-center.mb-4
      .col-lg-5(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img-05.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Notificacin al ICA
              p.text-center.mb-0 #[b Informar a la oficina del Instituto Colombiano Agropecuario (ICA)] ms cercana al lugar de la emergencia o accidente para recibir directrices de manejo.
      .col-lg-5(data-aos="fade-right")
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img-06.svg' alt="Imagen decorativa")
          .tarjeta.bg-color-3.w-100
            .p-4
              h4.text-center Coordinacin de autoridades
              p.text-center.mb-0 En caso de accidentes viales graves, se debe coordinar la respuesta con las #[b autoridades locales] (municipal o distrital)

    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 Fase C. Disposicin sanitaria final

    p.mb-4 Estas tcnicas se ejecutan bajo la orientacin del ICA y son esenciales para evitar riesgos biolgicos y sufrimiento prolongado; adems, deben tener en cuenta:

    .row.justify-content-center
      .col-lg-5.mb-4.mb-lg-0
        .tarjeta-avatar(style="padding-top: 0px !important;").h-100          
          .tarjeta.color-primarios.w-100(style="padding-top: 0px !important; background-color: #DAE4E3;")
            img(src='@/assets/curso/temas/tema3/img-15.png', alt='Imagen decorativa')
            .p-4
              h5 Sacrificio humanitario
              p El plan debe contemplar el #[b sacrificio] (eutanasia) de aquellos animales que presenten #[b sufrimiento irreversible] o lesiones que no puedan ser curadas.
              p.mb-0 Debe ser realizado por personal #[b competente y capacitado], utilizando mtodos humanitarios para minimizar el dolor.
      .col-lg-5
        .tarjeta-avatar(style="padding-top: 0px !important;").h-100            
          .tarjeta.color-primarios.w-100(style="padding-top: 0px !important; background-color: #DAE4E3;")
            img(src='@/assets/curso/temas/tema3/img-16.png', alt='Imagen decorativa')
            .p-4
              h5 Disposicin de cadveres
              p Se debe realizar la #[b disposicin sanitaria de los animales muertos] (cadveres) siguiendo estrictamente las directrices del ICA y las autoridades ambientales locales, evitando la contaminacin.
    
    Separador

    #t_3_2.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 3.2 Tcnicas de manejo de novedades y emergencias

    .bloque-texto-g.bloque-texto-g--inverso.color-secundario.p-3.p-sm-4.p-md-5.mb-4(style="background-color:#004E3D")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/tema3/img-17.png')})`}" data-aos="fade-left"
      )
      .bloque-texto-g__texto.p-4(data-aos="fade-right")
        p.mb-0 Son el conjunto de procedimientos operativos que deben ser ejecutados de forma inmediata por el personal (conductor, tripulante, propietario), al detectar cualquier situacin crtica que afecte el bienestar o la vida de los animales durante el transporte. Estas tcnicas estn integradas en el #[b plan de contingencia] y se enfocan en la #[b mitigacin del sufrimiento, la notificacin sanitaria y la disposicin segura] de la carga.

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Deteccin y activacin de la tcnica de manejo

    p.mb-4 La tcnica se activa tras la deteccin de cualquier evento grave (novedad) que ponga en riesgo la salud animal y debe tener en cuenta:

    .row.justify-content-center.mb-4
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 25.
          i Activacin de tcnicas de manejo
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Aspecto de la tcnica
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Detalle de la novedad (emergencia)
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (base de la activacin)
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Deteccin rutinaria]
                td(style= "background-color: #FFFF") La novedad se detecta durante la #[b inspeccin obligatoria cada cuatro (4) horas] en ruta o mediante observacin visual directa.
                td(style= "background-color: #FFFF") #[b Inspeccin cada 4 horas]. Manual de procedimiento (Nm. 4.g).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Novedades crticas]
                td(style= "background-color: #FFFF") El plan se activa ante animales #[b enfermos, muertos o accidentes] que generen animales #[b atrapados, cadas, traumatismos o fracturas.]
                td(style= "background-color: #FFFF") #[b Animales enfermos, muertos o accidentes]. ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6).
              tr(data-aos="fade-right")
