                  td(style= "background-color: #FFFF") Las medidas van desde la #[b ayuda o atencin inmediata al animal...] ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Reubicacin/acomodacin]
