                td(style= "background-color: #FFFF") #[b ]"informar a la oficina del Instituto Colombiano Agropecuario ICA ms cercana"]. ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6).
              tr(data-aos="fade-right")
