    #t_3_3.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 3.3 Procedimiento de Seguridad y Salud en el Trabajo (SST)

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-1.d-none.d-lg-block
        figure
          img.img-fluid(src="@/assets/curso/temas/tema3/img-18.svg" alt="Imagen decorativa")
      .col-lg-11
        p.mb-0(data-aos="fade-right") Este procedimiento para el personal involucrado en el transporte de animales en pie se fundamenta indirectamente en la normativa de bienestar animal (Resolucin 20223040006915), ya que la seguridad del operario est intrnsecamente ligada al manejo humanitario y las condiciones estructurales de la unidad de transporte. Al respecto, se deben tener en cuenta los siguientes procesos:

    AcordionA.color-secundario(tipo="a" clase-tarjeta="tarjeta BG03" data-aos="zoom-in")
      .row.justify-content-center(titulo="Marco de la seguridad laboral a travs del bienestar animal")
        .col-lg-12.order-lg-1.order-2
          p La SST del personal se rige por los siguientes principios del manual de procedimientos:
        .col-lg-10.order-lg-1.order-2
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 28.
            i Principios SST
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Principio
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Detalle y mitigacin del riesgo para el operario
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (base)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Capacitacin obligatoria]
                  td(style= "background-color: #FFFF") Todo operador (conductor/tripulante) debe realizar y aprobar el #[b curso de capacitacin en bienestar animal.] Este curso incluye las tcnicas de manejo que minimizan los riesgos de golpes o accidentes con los animales.
                  td(style= "background-color: #FFFF") #[b "Toda persona involucrada] en el proceso... deber #[b realizar un curso de capacitacin en bienestar animal..."]. Manual de procedimiento (Nm. 6).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Manejo de riesgo biolgico]
                  td(style= "background-color: #FFFF") La #[b limpieza y desinfeccin] peridica del vehculo reduce la exposicin del personal a agentes infecciosos (patgenos) presentes en las excretas, lo cual es un requisito de SST.
                  td(style= "background-color: #FFFF") El vehculo debe permitir su #[b "lavado y desinfeccin"]. Resolucin 20223040006915 (Nm. 2.2.c.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Manejo de riesgo fsico]
                  td(style= "background-color: #FFFF") El cumplimiento de la tcnica de #[b manejo humanitario] (sin hostigamiento) reduce la probabilidad de que el animal se defienda mediante patadas o mordeduras al sentirse amenazado.
                  td(style= "background-color: #FFFF") El manejo debe ser con #[b "calma, sin ruidos ni hostigamiento"]. Manual de procedimiento (Nm. 4).
      
      .row.justify-content-center(titulo="Procedimientos de seguridad laboral derivados de la infraestructura")
        .col-lg-12.order-lg-1.order-2
          p Los requisitos tcnicos del vehculo son procedimientos de seguridad que protegen tanto al animal como al operario durante el cargue y el manejo de la carga. Estos, deben tener en cuenta:
        .col-lg-10.order-lg-1.order-2
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 29.
            i Requisitos del vehculo
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Procedimiento de seguridad
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Detalle y mitigacin de riesgos (SST)
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Riesgo de cadas y sobreesfuerzo]
                  td(style= "background-color: #FFFF") #[b Rampas seguras:] uso de rampas con #[b pisos antideslizantes] y una #[b inclinacin mxima de 20 grados.] Esto reduce el riesgo de cadas y lesiones lumbares para el operario al guiar la carga.
                  td(style= "background-color: #FFFF") "...con #[b pisos de superficies antideslizante...] y con #[b mximo 20 grados de inclinacin..."]. Manual de procedimiento (Nm. 4.a).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Riesgo de lesiones por fauna]
                  td(style= "background-color: #FFFF") #[b Paredes lisas:] las superficies internas deben ser lisas y sin salientes. Esto minimiza el riesgo de que el operario se lesione al intervenir o #[b auxiliar a un animal] dentro del corral.
                  td(style= "background-color: #FFFF") #[b "Sern lisas, seguras, sin salientes, ni bordes puntiagudos..."]. Resolucin 20223040006915 (Nm. 2.2.d.i).

      .row.justify-content-center(titulo="Procedimiento de seguridad en emergencias")
        .col-lg-12.order-lg-1.order-2
          p El #[b plan de contingencia] exige que el operario aplique tcnicas de manejo de novedades que inherentemente son de alto riesgo para su seguridad fsica.
        .col-lg-10.order-lg-1.order-2
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 30.
            i Tcnicas de manejo del operario
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tcnica de manejo de emergencias
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") IRiesgo laboral asociado
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (base)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Ayuda inmediata]
                  td(style= "background-color: #FFFF") El operador debe entrar al rea de la carga para prestar #[b ayuda o atencin inmediata al animal] (cado, atrapado). Esto lo expone directamente a patadas, mordeduras y aplastamiento por parte de otros animales.
                  td(style= "background-color: #FFFF") El plan exige #[b ayuda o atencin inmediata al animal]. ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Disposicin de cadveres]
                  td(style= "background-color: #FFFF") El operador participa en la #[b disposicin de animales muertos], lo cual lo expone a #[b riesgos biolgicos y al riesgo de sobreesfuerzo] (lesiones lumbares) al manipular el cadver.
                  td(style= "background-color: #FFFF") El plan incluye la #[b disposicin de los animales muertos.] ABC Manual de Procedimientos para el Transporte, Manejo y Movilizacin de Animales en Pie (pg. 6).

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema3/img-19.svg' alt="Imagen decorativa")
          .tarjeta.color-primario2.w-100(style="padding-top: 5px !important")
            .text-white.p-4
              h4.text-center(style="margin-top: 42px;") Conclusin de SST
              p.text-center.mb-0 Aunque la normativa anexada no regula directamente la SST del personal, las obligaciones de #[b manejo humanitario, rampas seguras y bioseguridad] actan como procedimientos de control de riesgos para el trabajador y la #[b capacitacin obligatoria] es el vehculo para impartir el conocimiento de SST necesario para gestionar estos riesgos.

    Separador

    #t_3_4.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 3.4 Tipos y medidas de prevencin y proteccin

    p Las #[b medidas de prevencin y proteccin] son tcnicas y requisitos obligatorios que se implementan en el transporte de animales en pie, con el fin de #[b evitar el sufrimiento y el dolor] (prevencin) y #[b salvaguardar la integridad fsica y la salud] (proteccin) de los animales durante todas las fases de la movilizacin.
    p.mb-4 Las medidas se clasifican en tres tipos principales: #[b estructurales, operacionales] y de #[b bioseguridad:]

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Medidas de prevencin y proteccin estructurales (vehculo)

    p.mb-4 Estas medidas se relacionan con el diseo fsico del vehculo, cuya funcin es proteger a los animales de lesiones y factores ambientales, basadas en lo siguiente:

    .row.justify-content-center.mb-0
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 31.
          i Medidas estructurales
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tipo de medida
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Requisito de prevencin y proteccin 
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Proteccin contra impacto y lesiones]
                td(style= "background-color: #FFFF") Las unidades de transporte deben ser #[b lisas, seguras, sin salientes ni bordes puntiagudos] que puedan causar contusiones, laceraciones o lesiones.
                td(style= "background-color: #FFFF") "Sern #[b lisas, seguras, sin salientes, ni bordes puntiagudos] que puedan causar lesiones...". Resolucin 20223040006915 (Nm. 2.2.d.i).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Proteccin contra cadas]
                td(style= "background-color: #FFFF") Los pisos deben ser de #[b superficies antideslizantes] y no deben presentar #[b hoyos u orificios]. Las rampas deben tener #[b pisos antideslizantes] y una inclinacin mxima de #[b 20 grados].
                td(style= "background-color: #FFFF") Rampas con #[b "pisos de superficies antideslizante], que no #[b presenten salientes puntiagudas, hoyos u orificios] y con #[b mximo 20 grados de inclinacin..."]. Manual de procedimiento (Nm. 4.a).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Proteccin climtica]
                td(style= "background-color: #FFFF") El vehculo debe contar con #[b techo o cubierta] que proteja a los animales de la #[b radiacin solar directa, lluvia o granizo] (factores que causan estrs trmico).
                td(style= "background-color: #FFFF") "...debern contar con #[b techo o cubierta] que proteja a los animales de las #[b condiciones climticas adversas"]. Resolucin 20223040006915 (Nm. 2.2.c.i).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Proteccin social y fsica]
                td(style= "background-color: #FFFF") Uso de #[b divisiones o barreras fuertes] para separar grupos hostiles (con cuernos) o de diferente tamao, protegiendo a los ms vulnerables del conflicto social.
                td(style= "background-color: #FFFF") "Contar con #[b divisiones o barreras fuertes] que permitan la #[b separacin de los animales"]. Manual de procedimiento (Nm. 2.2.c.iii).

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Medidas de prevencin y proteccin operacionales (procedimientos)

    .BGM01.px-md-5.px-4.mb-4
      p.mb-4 Estas medidas se refieren a las tcnicas de manejo y planificacin que deben ejecutar el personal para prevenir el sufrimiento y la fatiga; dichas medidas presentan los siguientes tipos:

      .row.justify-content-center.mb-0
        .col-lg-10
          .titulo-sexto.color-acento-contenido(data-aos="fade-right")
            h5 Tabla 32.
            i Medidas operacionales
          .tabla-a.color-acento-contenido.mb-4
            table
              thead
                tr
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tipo de medida
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tcnica de prevencin y proteccin
                  th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
              tbody
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Prevencin sanitaria (seleccin)]
                  td(style= "background-color: #FFFF") #[b Excluir] del transporte a animales #[b no aptos]: enfermos, lesionados, con prolapsos o hembras en #[b ltimo tercio de gestacin]. Esta es la medida de prevencin inicial.
                  td(style= "background-color: #FFFF") Animales que #[b "no puedan permanecer en la posicin de estacin de equilibrio esttico animal sin ayuda"] [y otros criterios de exclusin]. Manual de procedimiento (Nm. 4.a.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Proteccin contra el estrs y hacinamiento]
                  td(style= "background-color: #FFFF") #[b Controlar la densidad] para procurar que se cuente con #[b espacio disponible] suficiente para que el animal pueda #[b levantarse, acostarse, dar vuelta].
                  td(style= "background-color: #FFFF") "...procurar que se cuente con #[b espacio disponible] para que el animal pueda #[b levantarse, acostarse, dar vuelta..."]. Manual de procedimiento (Nm. 4.e).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Prevencin de fatiga (personal)]
                  td(style= "background-color: #FFFF") El conductor debe #[b descansar al menos ocho (8) horas] tras ocho (8) horas de conduccin. Esto previene accidentes por fatiga.
                  td(style= "background-color: #FFFF") #[b "descansar al menos ocho (8) horas], una vez haya conducido #[b ocho (8) horas continuas"]. Manual de procedimiento (Nm. 4.i).
                tr(data-aos="fade-right")
                  td.text-center(style= "background-color: #FFFF") #[b Prevencin de sufrimiento por negligencia]
                  td(style= "background-color: #FFFF") El personal debe realizar #[b inspeccin] de la carga por lo menos cada #[b cuatro (4) horas] para detectar y atender emergencias.
                  td(style= "background-color: #FFFF") "El conductor o tripulante deber realizar la #[b inspeccin...] por lo menos #[b cada cuatro (4) horas] durante el viaje". Manual de procedimiento (Nm. 4.g).

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/ico.svg', alt='Imagen decorativa')
      h3.mb-0 Medidas de prevencin y proteccin de bioseguridad y salud

    p.mb-4 Estas medidas se enfocan en la prevencin de riesgos biolgicos y la proteccin contra el estrs trmico.

    .row.justify-content-center.mb-0
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-right")
          h5 Tabla 33.
          i Medidas de bioseguridad
        .tabla-a.color-acento-contenido.mb-4
          table
            thead
              tr
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tipo de medida
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Tcnica de prevencin y proteccin
                th(data-aos="fade-down")(style= "background-color: #DAE4E3; width: 16.6%") Soporte documental (texto exacto)
            tbody
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Prevencin de contaminacin cruzada]
                td(style= "background-color: #FFFF") #[b Lavado y desinfeccin] de la unidad de transporte antes y despus de cada uso, para eliminar patgenos de lotes anteriores.
                td(style= "background-color: #FFFF") El material debe facilitar su #[b "lavado y desinfeccin"]. Resolucin 20223040006915 (Nm. 2.2.c.i).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Proteccin sanitaria ambiental]
                td(style= "background-color: #FFFF") El piso debe contar con un #[b sistema que evite la filtracin de orina y heces hacia la va pblica], previniendo la dispersin de riesgos biolgicos.
                td(style= "background-color: #FFFF") "...contar con un #[b sistema que evite la filtracin de orina y heces hacia la va pblica"]. Resolucin 20223040006915 (Nm. 2.2.c.ii).
              tr(data-aos="fade-right")
                td.text-center(style= "background-color: #FFFF") #[b Proteccin contra estrs trmico]
                td(style= "background-color: #FFFF") Mantener la #[b ventilacin constante] y asegurar la temperatura que no podr ser inferior a 5 #[b C ni superior a 30 C].
                td(style= "background-color: #FFFF") #[b "ventilacin constante] y asegurar la temperatura que no podr ser inferior a #[b 5 C ni superior a 30 C"]. Manual de procedimiento (Nm. 4.f).
